<template>
    <p-data-table
        :value="testResults"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
        v-model:selection="selectedBloodTest"
        selectionMode="single"
        dataKey="testOrderId"
    >
        <p-column header="ID" field="testOrderId" />
        <p-column header="Order Date" field="bloodTestOrderDate" />
        <p-column header="E2" field="etwo">
            <template #body="{data}">
                {{
                    data.etwo === null || data.etwo === ""
                        ? data.etwoStat
                        : data.etwo
                }}
            </template>
        </p-column>
        <p-column header="LH" field="lh">
            <template #body="{data}">
                {{ data.lh === null || data.lh === "" ? data.lhStat : data.lh }}
            </template>
        </p-column>
        <p-column header="PROG" field="prog">
            <template #body="{data}">
                {{
                    data.prog === null || data.prog === ""
                        ? data.progStat
                        : data.prog
                }}
            </template>
        </p-column>
        <p-column header="FSH" field="fsh">
            <template #body="{data}">
                {{
                    data.fsh === null || data.fsh === ""
                        ? data.fshStat
                        : data.fsh
                }}
            </template>
        </p-column>
        <p-column header="TSH" field="tsh">
            <template #body="{data}">
                {{
                    data.tsh === null || data.tshStat == ""
                        ? data.tshStat
                        : data.tsh
                }}
            </template>
        </p-column>
        <p-column header="PRL" field="prl">
            <template #body="{data}">
                {{
                    data.prl === null || data.prl === ""
                        ? data.prlStat
                        : data.prl
                }}
            </template>
        </p-column>
        <p-column header="BhCG" field="bhcg">
            <template #body="{data}">
                {{
                    data.bhcg === null || data.bhcg === ""
                        ? data.bhcgStat
                        : data.bhcg
                }}
            </template>
        </p-column>
        <p-column header="DHEAS" field="dheas">
            <template #body="{data}">
                {{
                    data.dheas === null || data.dheas === ""
                        ? data.dheasStat
                        : data.dheas
                }}
            </template>
        </p-column>
        <p-column header="AMHP" field="amh">
            <template #body="{data}">
                {{
                    data.amh === null || data.amh === ""
                        ? data.amhStat
                        : data.amh
                }}
            </template>
        </p-column>
        <p-column header="" style="width: 50px">
            <template #body="{data}">
                <p-button
                    label="Barcode"
                    class="p-button-sm"
                    @click="showBarcodeV2(data)"
                />
            </template>
        </p-column>

        <template #footer>
            <p-button label="Order Test" class="p-mr-2" @click="add" />
            <p-button label="Edit" class="p-mr-2" @click="edit" />
        </template>
    </p-data-table>
</template>

<script>
import { useBloodTestsReportInit } from "@/composable/blood-tests";
import { onMounted, onUnmounted } from "vue";
import { useToast } from "primevue/usetoast";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const {
            testResults,
            loading,
            getBloodTestResults,
            getBloodTestResultsWithOpenBarcode,
            orderBloodTestConfirmDialog,
            editDailogVisible,
            selectedBloodTest,
            showBarcode,
            showBarcodeV2
        } = useBloodTestsReportInit();

        const toast = useToast();

        onMounted(() => {
            getBloodTestResults();

            emitter.on(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.on(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );
        });

        onUnmounted(() => {
            emitter.off(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.off(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );
        });

        return {
            testResults,
            selectedBloodTest,
            loading,
            showBarcode,
            showBarcodeV2,
            add: () => {
                orderBloodTestConfirmDialog.value = true;
            },
            edit: () => {
                if (selectedBloodTest.value == null) {
                    toast.add({
                        severity: "warn",
                        summary: "Select Row",
                        detail: "Please select a row first",
                        life: 3000
                    });
                    return;
                }
                editDailogVisible.value = true;
            }
        };
    }
};
</script>
